import { Component, OnInit, Renderer, ViewContainerRef } from '@angular/core';
import {
  UserProfile,
  PhotoAlbum,
  ProfileFeed,
  AlbumEntry,
  GeneratedLinks
} from '../../models';
import { Observable } from 'rxjs/Observable';
import { FetcherService } from '../../core/services';
import { ClipboardService } from 'ngx-clipboard';
import { ToastsManager } from 'ng2-toastr';

@Component({
  selector: 'app-google-dashboard',
  templateUrl: './google-dashboard.component.html',
  styleUrls: ['./google-dashboard.component.css']
})
export class GoogleDashboardComponent implements OnInit {
  readonly emailKey = 'google-email';

  email: string;
  userProfile: UserProfile;

  userAlbums: Array<AlbumEntry>;

  currentAlbum: PhotoAlbum;

  userProfileFeed: ProfileFeed;

  loading = false;

  albumLoading = false;

  constructor(
    private photoFetcher: FetcherService,
    private clipboardService: ClipboardService,
    private renderer: Renderer,
    public toastr: ToastsManager,
    vcr: ViewContainerRef
  ) {
    this.toastr.setRootViewContainerRef(vcr);
  }

  ngOnInit() {
    this.email = localStorage.getItem(this.emailKey);
  }

  private getProfile(email) {
    this.loading = true;
    this.photoFetcher
      .getUserProfile(email)
      .finally(() => (this.loading = false))
      .subscribe((d: ProfileFeed) => {
        this.userProfileFeed = d;
        this.userProfile = d.userProfile;
        this.userAlbums = d.albums;
        localStorage.setItem(this.emailKey, email);
      });
  }

  public onUserId(userId: string) {
    this.getProfile(userId);
  }

  private onAlbum(albumId: string) {
    if (!albumId) {
      console.error('albumId undefined');
      return;
    }
    console.log('album clicked: ' + albumId);
    this.albumLoading = true;
    this.currentAlbum = null;
    this.photoFetcher
      .getAlbum(this.userProfile.userId, albumId)
      .finally(() => (this.albumLoading = false))
      .subscribe(data => {
        this.currentAlbum = data;
      });
  }

  onLinksExported(generatedLinks: GeneratedLinks) {
    console.log('received links: ' + generatedLinks.numberOfPhotos);
    this.clipboardService.copyFromContent(
      generatedLinks.linksHtml,
      this.renderer
    );
    this.toastr.success(
      `${generatedLinks.numberOfPhotos} links have been copied to clipboard`,
      null
    );
  }
}



