import {
  Component,
  OnInit,
  Input,
  OnChanges,
  SimpleChanges,
  Output,
  EventEmitter
} from '@angular/core';
import { PhotoAlbum, Photo, GeneratedLinks } from '../../../models';
import { ExportService, TemplateService } from '../../../core/services';
// tslint:disable-next-line:import-blacklist
import { Observable } from 'rxjs/Rx';
// import value from '*.json';

@Component({
  selector: 'app-album',
  templateUrl: './album.component.html',
  styleUrls: ['./album.component.scss']
})
export class AlbumComponent implements OnInit, OnChanges {
  @Input() album: PhotoAlbum;
  @Output() exportedLinks = new EventEmitter<GeneratedLinks>();

  template: string;
  clipboardText: string;
  selectedSize: number;
  sizes = [144, 288, 320, 400, 640, 720, 800, 912, 1024, 1280, 1600];
  notificationMessage: string;

  photoSizes: Array<any> = new Array<any>();

  constructor(
    private exportService: ExportService,
    private templateService: TemplateService
  ) {}

  ngOnInit() {
    this.sizes.forEach(element => {
      this.photoSizes.push({ name: element + 'px', value: element });
    });

    this.selectedSize = 800;
    this.templateService
      .getTemplate()
      .subscribe(data => (this.template = data));
  }

  ngOnChanges(changes: SimpleChanges): void {
    const albumChange = changes['album'];
    if (albumChange) {
      const val = albumChange.currentValue as PhotoAlbum;
      const photos = val.photos;
      this.album = val;
      this.album.photos = photos.sort((a, b) => {
        if (a.updated < b.updated) {
          return 1;
        }

        if (a.updated === b.updated) {
          return 0;
        }

        return -1;
      });
    }
  }

  selectAction(action: string) {
    console.log('action selected: ' + action);
  }

  checkPhotos(state: boolean) {
    this.album.photos.forEach(element => {
      element.checked = state;
    });
  }

  invertCheck() {
    this.album.photos.forEach(x => (x.checked = !x.checked));
  }

  getSelecedPhotos(): Array<Photo> {
    return this.album.photos.filter(x => x.checked);
  }

  exportLinks() {
    const content = 'super content';
    const selectedPhotos = this.getSelecedPhotos();
    this.exportService
      .generateLinksTemplate(
        this.album,
        selectedPhotos,
        this.selectedSize,
        this.template
      )
      .subscribe(html => {
        console.log('got clipboardValue: ' + html);

        this.exportedLinks.emit(<GeneratedLinks>{
          numberOfPhotos: selectedPhotos.length,
          linksHtml: html
        });
      });

    console.log('links are copied to clipboard');
  }

  anyCheckedPhotos(): boolean {
    return !!this.album.photos.find(x => x.checked);
  }

  numberOfCheckedPhotos(): number {
    const num = this.album.photos.filter(x => x.checked);
    return num.length;
  }

  onTemplateChanged(value: string) {
    console.log('template changed to ' + value);
    this.template = value;
    this.templateService.saveTemplate(value).subscribe();
  }
}



