import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { UserProfileComponent } from './user-profile/user-profile.component';
import { FormsModule } from '@angular/forms';
import { DisplayUserProfileComponent } from './display-user-profile/display-user-profile.component';
import { SpinLoaderComponent } from './spin-loader/spin-loader.component';
import { AlbumListComponent } from './album-list/album-list.component';
import { AlbumComponent } from './album/album.component';
import { AlbumHeaderComponent } from './album-header/album-header.component';
import { ExportTemplateComponent } from './export-template/export-template.component';
import { CoreModule } from '../../core/core.module';
import { ClipboardModule } from 'ngx-clipboard';
import { CollapseModule } from 'ngx-bootstrap/collapse';
import { AlbumListItemComponent } from './album-list-item/album-list-item.component';
import { AlbumPaginatorComponent } from './album-paginator/album-paginator.component';

@NgModule({
  imports: [CommonModule, FormsModule, CoreModule, ClipboardModule],
  declarations: [
    UserProfileComponent,
    DisplayUserProfileComponent,
    SpinLoaderComponent,
    AlbumListComponent,
    AlbumComponent,
    AlbumHeaderComponent,
    ExportTemplateComponent,
    AlbumListItemComponent,
    AlbumPaginatorComponent
  ],
  exports: [
    UserProfileComponent,
    DisplayUserProfileComponent,
    SpinLoaderComponent,
    AlbumListComponent,
    AlbumComponent,
    AlbumHeaderComponent,
    ExportTemplateComponent
  ]
})
export class FetchComponentsModule {}



