import {
  Component,
  OnInit,
  Input,
  EventEmitter,
  Output,
  OnChanges,
  SimpleChanges
} from '@angular/core';
import { UserProfile, ProfileFeed } from '../../../models';
import 'rxjs/add/operator/catch';
import 'rxjs/add/operator/finally';
import 'rxjs/add/operator/map';
import { Observable } from 'rxjs/Observable';

@Component({
  selector: 'app-user-profile',
  templateUrl: './user-profile.component.html',
  styleUrls: ['./user-profile.component.scss']
})
export class UserProfileComponent implements OnInit, OnChanges {
  @Input() email: string;
  @Output() onUserId = new EventEmitter<string>();

  emailUserPart: string;
  emailDomainPart: string;
  constructor() {}

  ngOnChanges(changes: SimpleChanges): void {
    const emailChanges = changes['email'];
    if (emailChanges && emailChanges.currentValue) {
      this.setEmailPart(emailChanges.currentValue);
    }
  }

  setEmailPart(email: string) {
    const reg = new RegExp('(?<user>.*)@(?<domain>.*)');
    const m = reg.exec(email);
    if (!m) {
      console.error('Error splitting email: ' + email);
    }
    const user = m[1];
    const domain = m[2];
    this.emailUserPart = user;
    this.emailDomainPart = domain;
  }

  ngOnInit() {}

  onFetchProfile(email) {
    this.onUserId.emit(email);
  }

  errorHandler(error: any) {
    return 'super-mega-error!!';
  }

  mapper(data: any) {}
}



