import {
  Component,
  OnInit,
  Input,
  OnChanges,
  SimpleChanges,
  EventEmitter,
  Output
} from '@angular/core';
import { PhotoAlbum, AlbumEntry, Page } from '../../../models';

const albumsPerPage = 5;

@Component({
  selector: 'app-album-list',
  templateUrl: './album-list.component.html',
  styleUrls: ['./album-list.component.scss']
})
export class AlbumListComponent implements OnInit, OnChanges {
  @Input() albums: Array<AlbumEntry>;
  @Output() onAlbumClicked = new EventEmitter<string>();

  pages: Array<any> = new Array<any>();
  albumsCount: number;

  /* paging */
  albumsPerPage = albumsPerPage;
  frameSize = 10;

  albumsDisplayed: Array<AlbumEntry>;
  activeAlbumId: string;
  numberOfPages: number;
  currentPage: Page;

  ngOnChanges(changes: SimpleChanges): void {
    const albumsListChange = changes['albums'];
    if (albumsListChange) {
      const albumList = albumsListChange.currentValue as Array<AlbumEntry>;
      if (albumList) {
        this.albumsCount = albumList.length;
        if (this.albumsCount > albumsPerPage) {
          this.loadPageNumber(0, albumsPerPage);
        } else {
          this.albumsDisplayed = this.albums;
        }
      }
    }
  }

  constructor() {}

  ngOnInit() {}

  fetchAllAlbums() {}

  loadAlbum(albumId) {
    this.activeAlbumId = albumId;
    this.onAlbumClicked.emit(albumId);
  }

  loadPage(page: Page) {
    this.albumsDisplayed = this.albums.splice(page.startIndex, page.itemsCount);
  }

  // 1 based number
  loadPageNumber(startIndex, numberOfItems: number) {
    this.albumsDisplayed = this.albums.splice(startIndex, numberOfItems);
  }
}



