import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { AlbumEntry } from '../../../models';

@Component({
  selector: 'app-album-list-item',
  templateUrl: './album-list-item.component.html',
  styleUrls: ['./album-list-item.component.scss']
})
export class AlbumListItemComponent implements OnInit {
  @Input() album: AlbumEntry;
  @Input() active: boolean;
  @Output() albumClicked = new EventEmitter<string>();

  constructor() {}

  ngOnInit() {}

  loadAlbum() {
    this.albumClicked.emit(this.album.id);
  }
}



