import { EntityBase } from './entity-base';

export class Photo extends EntityBase {
  albumId: string;
  width: number;
  height: number;
  size: number;
  tags: Array<string>;
  thumbnail_sm: string;
  thumbnail_md: string;
  thumbnail_lg: string;
  // content
  url: string;
  checked: boolean;
}



