import { Injectable } from '@angular/core';
import {
  PhotoAlbum,
  ProfileFeed,
  UserProfile,
  AlbumEntry,
  Photo
} from '../../models';

@Injectable()
export class PicasaMapperService {
  constructor() {}

  parsePhotoAlbum(feed: any) {
    const photoAlbum = new PhotoAlbum();
    photoAlbum.id = feed.gphoto$id.$t;
    photoAlbum.title = feed.title.$t;

    const publishedInt = parseInt(feed.gphoto$timestamp.$t, 10);
    photoAlbum.published = new Date(publishedInt);
    const parsedDate = Date.parse(feed.updated.$t);
    photoAlbum.updated = new Date(parsedDate);
    photoAlbum.thumbnail = feed.icon.$t;

    const foundLink = this.getLink(feed.link);

    photoAlbum.url = foundLink;

    photoAlbum.numberOfPhotos = Number(feed.gphoto$numphotos.$t);

    const photos = new Array<Photo>();

    const entries = <Array<any>>feed.entry;
    for (const entry of entries) {
      const parsed = this.parsePhoto(entry);
      photos.push(parsed);
    }

    photoAlbum.photos = photos;
    return photoAlbum;
  }

  parsePhoto(entry: any): Photo {
    const photo = {
      id: entry.gphoto$id.$t,
      title: entry.title.$t,
      published: new Date(entry.published.$t),
      updated: new Date(entry.updated.$t),
      checked: false,
      albumId: entry.gphoto$albumid.$t,
      height: parseInt(entry.gphoto$height.$t, 10),
      size: parseInt(entry.gphoto$size.$t, 10),
      width: parseInt(entry.gphoto$width.$t, 10),
      tags: null,
      thumbnail: entry.media$group.media$content[0].url
    } as Photo;

    photo.url = entry.content.src;
    photo.thumbnail_sm = entry.media$group.media$thumbnail[0].url;
    photo.thumbnail_md = entry.media$group.media$thumbnail[1].url;
    photo.thumbnail_lg = entry.media$group.media$thumbnail[2].url;

    return photo;
  }

  parseProfileFeed(feed: any): ProfileFeed {
    const profile = this.parseUserProfile(feed);
    const albums = this.parseAlbums(feed);

    const profileFeed: ProfileFeed = {
      userProfile: profile,
      albums: albums
    };
    return profileFeed;
  }

  parseUserProfile(feed: any): UserProfile {
    const profile = new UserProfile();
    profile.id = feed.id.$t;
    profile.title = feed.title.$t;
    profile.url = feed.icon.$t;
    profile.author = feed.author.length > 0 ? feed.author[0].name.$t : '';
    profile.authorUrl = '';
    profile.albumCount = feed.openSearch$totalResults.$t;
    profile.userId = feed.gphoto$user.$t;
    profile.thumbnail = feed.gphoto$thumbnail.$t;
    return profile;
  }

  parseAlbum(entry: any): AlbumEntry {
    const album = new AlbumEntry();
    album.id = entry.gphoto$id.$t;

    album.title = entry.title.$t;
    if (entry.media$group) {
      album.thumbnail = entry.media$group.media$thumbnail[0].url;
    }

    album.updated = new Date(entry.updated.$t);

    if (entry.published) {
      album.published = new Date(entry.published.$t);
    } else {
      if (entry.gphoto$timestamp) {
        album.published = new Date(entry.gphoto$timestamp.$t);
      }
    }

    const link = this.getLink(entry.link);

    album.url = link;

    album.numberOfPhotos = Number(entry.gphoto$numphotos.$t);
    album.author = entry.author[0].name.$t;
    return album;
  }

  getLink(links: any) {
    const linksArray = <Array<any>>links;

    const foundLink = links.find(
      x => x.type === 'text/html' && x.rel === 'alternate'
    );

    if (foundLink) {
      return foundLink.href;
    }

    return null;
  }

  parseAlbums(feed: any): Array<AlbumEntry> {
    const albums = new Array<AlbumEntry>();

    const entries = <Array<any>>feed.entry;
    for (const entry of entries) {
      const parsed = this.parseAlbum(entry);
      albums.push(parsed);
    }

    return albums;
  }
}



