import {
  Component,
  OnInit,
  Input,
  Output,
  EventEmitter,
  OnChanges,
  SimpleChanges
} from '@angular/core';
import { Observable } from 'rxjs/Observable';
import { Page } from '../../../models';

@Component({
  selector: 'app-album-paginator',
  templateUrl: './album-paginator.component.html',
  styleUrls: ['./album-paginator.component.scss']
})
export class AlbumPaginatorComponent implements OnInit, OnChanges {
  @Input() albumsPerPage: number;
  @Input() albumCount: number;
  @Input() frameSize: number;
  @Output() pageSelected = new EventEmitter<Page>();
  pages: Array<Page> = new Array<Page>();
  numberOfPages: number;

  totalPages: Array<Page> = new Array<Page>();

  activePage: Page;

  startFrameIndex: number;
  endFrameIndex: number;
  constructor() {
    this.startFrameIndex = 0;
    this.endFrameIndex = 0;
  }

  ngOnInit() {}

  ngOnChanges(changes: SimpleChanges): void {
    // const albumsPerPageChange = changes['albumsPerPage'];
    // if (albumsPerPageChange) {
    //   this.startCalculation().subscribe(() => {});
    // }
    if (this.albumCount && this.albumsPerPage) {
      this.recalculatePages(this.albumCount, this.albumsPerPage);
    }
  }

  movePageNext() {
    if (!this.activePage) {
      return;
    }

    const activePageIndex = this.activePage.number - 1;
    if (activePageIndex !== null && activePageIndex < this.pages.length - 1) {
      this.loadPage(this.pages[activePageIndex + 1]);
    }
  }

  movePageBack() {
    if (!this.activePage) {
      return;
    }

    const activePageIndex = this.activePage.number - 1;
    if (!!activePageIndex) {
      this.loadPage(this.pages[activePageIndex - 1]);
    }
  }

  moveToNextFrame() {
    if (!this.activePage) {
      return;
    }

    const activePageIndex = this.activePage.number - 1;
    if (activePageIndex !== null && activePageIndex < this.pages.length - 1) {
      this.loadPage(this.pages[activePageIndex + 1]);
    }
  }

  recalculatePages(totalCount: number, pagesPerPage: number) {
    this.pages = [];
    this.numberOfPages = Math.ceil(totalCount / pagesPerPage);

    for (let i = 0; i < this.numberOfPages; i++) {
      const itemsCount = pagesPerPage;

      this.totalPages.push({
        first: i === 0,
        active: i === 0,
        number: i + 1,
        startIndex: i * pagesPerPage,
        itemsCount: itemsCount,
        last: i === this.numberOfPages - 1
      } as Page);
    }

    this.pages = this.totalPages.slice(0, this.frameSize);
    this.activePage = this.pages.find(x => x.active);
  }

  loadPage(page: Page) {
    if (this.activePage) {
      this.activePage.active = false;
    }

    page.active = true;
    this.pageSelected.emit(page);
    this.activePage = page;
  }
}



