import { Injectable } from '@angular/core';
import { PhotoAlbum, ProfileFeed } from '../../models';
import { Observable } from 'rxjs/Observable';

@Injectable()
export abstract class FetcherService {
  abstract getAlbum(userId: string, albumId: string): Observable<PhotoAlbum>;

  abstract getUserId(email: string): Observable<string>;

  abstract getUserProfile(userId: string): Observable<ProfileFeed>;
}



