import { Injectable } from '@angular/core';
import { Observable } from 'rxjs/Observable';
import 'rxjs/add/observable/of';
import { PhotoAlbum, Photo } from '../../models';

@Injectable()
export class ExportService {
  constructor() {}

  generateLinksTemplate(
    album: PhotoAlbum,
    selectedPhotos: Array<Photo>,
    size: number,
    template
  ): Observable<string> {
    let header = '';

    if (album.title) {
      header = album.title + '\n<br />';
    }

    const photoLinks = new Array<string>();

    selectedPhotos.filter(x => x.checked).forEach((element, index) => {
      const reg = new RegExp('(?<path>http.*)/(?<name>.*)');
      const m = reg.exec(element.thumbnail);
      const path = m[1];
      const name = m[2];
      const exportItem = template
        .replace('%index%', index + 1)
        .replace('%href%', element.url)
        .replace('%src%', `${path}/s${size}/${name}`)
        .replace('{{title}}', element.title);

      photoLinks.push(exportItem);
    });

    const photos = photoLinks.join('\n');

    let links = '';
    if (header) {
      links = header + '\n' + photos;
    } else {
      links = photos;
    }
    return Observable.of(links);
  }
}



