import { Injectable } from '@angular/core';
import { Observable } from 'rxjs/Observable';

const storageKey = 'photofetcher-template';

@Injectable()
export class TemplateService {
  constructor() {}

  getTemplate(): Observable<string> {
    let storedTemplate = localStorage.getItem(storageKey);

    if (!storedTemplate) {
      storedTemplate = '<a href="%href%">' + '<img src="%src%"/>' + '</a>';
    }

    return Observable.of(storedTemplate);
  }

  saveTemplate(template: string): Observable<{}> {
    localStorage.setItem(storageKey, template);
    return Observable.of();
  }
}



