import {
  Component,
  OnInit,
  Input,
  Output,
  EventEmitter,
  OnChanges,
  SimpleChanges
} from '@angular/core';

@Component({
  selector: 'app-export-template',
  templateUrl: './export-template.component.html',
  styleUrls: ['./export-template.component.scss']
})
export class ExportTemplateComponent implements OnInit, OnChanges {
  @Input() template: string;
  @Output() templateChanged = new EventEmitter<string>();

  _model: string;
  get model(): string {
    return this._model;
  }

  set model(value: string) {
    this._model = value;
    this.templateChanged.emit(value);
  }

  isCollapsed: boolean;
  constructor() {
    this.template = 'test template';
    this.isCollapsed = true;
  }

  ngOnChanges(changes: SimpleChanges): void {
    const templateChange = changes['template'];
    if (templateChange) {
      this._model = templateChange.currentValue;
    }
  }

  ngOnInit() {}
}



